<template>
    <div>
        <md-table v-model="reportData" class="main-table" md-card>
            <md-table-toolbar>
                <h1 class="md-title"> </h1>
            </md-table-toolbar>
            <md-table-row slot="md-table-row" slot-scope="{ item }" >
                <md-table-cell md-label="">{{ item.name }}</md-table-cell>
                <md-table-cell md-label="">{{ item.value }}</md-table-cell>
            </md-table-row>
        </md-table>
        <h1 class="md-title">  </h1>
        <div class="charts">
            <div class="employees-chart report-chart">
                <line-chart :chart-data="employeesChartData"></line-chart>
            </div>
            <div class="runes-chart report-chart">
                <line-chart :chart-data="runesChartData"></line-chart>
            </div>
        </div>
    </div>
</template>


<script>
import LineChart from '@/charts/LineChart.js'
export default {
    name: 'BaseReport',
    components: {
      LineChart
    },
    mounted() {
        this.renderCharts();
    },
    data() {
        return {
            months: ['', '', '', '', ''],
            employeesChartData: null,
            runesChartData: null,
            reportData: [
                {
                    name: ' ',
                    value: 5
                },
                {
                    name: '  ',
                    value: 15
                },
                {
                    name: '  ',
                    value: 27
                },
                {
                    name: '  ',
                    value: 14
                },
                {
                    name: '  ',
                    value: 3
                }]
        }
    },
    methods: {
        renderCharts() {
            this.renderEmployeesChart();
            this.renderRunesChart();
        },
        renderEmployeesChart() {
            this.employeesChartData = {
                labels: this.months,
                datasets: [
                    {
                        label: ' ',
                        backgroundColor: '#0f52bf',
                        data: [1,2,3,3,5]
                    }
                ]
            };
        },
        renderRunesChart() {
            this.runesChartData = {
                labels: this.months,
                datasets: [
                    {
                        label: '  ',
                        backgroundColor: '#95a014',
                        data: [1,3,10,7,3]
                    }
                ]
            };
        }
    }
}
</script>

<style>
.report-chart {
    max-width: 500px;
}
</style>
